.navlist {
    white-space: nowrap;
    font-size: 16px;
    -webkit-padding-start: 0;
    -webkit-margin-after: 0;
    -webkit-margin-before: 0;
    padding-top: 0px;
    margin-bottom: 0px;
    margin-top: 0px;
    padding-bottom: 0px;
    padding-right: 0px;
    padding-left: 0px;
    margin-right: 0px;
    margin-left: 0px;
    /* position: relative; */
    /* font-family: 'Open Sans', arial, sans-serif; */
    text-transform: none;
    letter-spacing: normal;
    box-sizing: inherit;
    display: block;
    list-style-type: disc;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    line-height: 24px;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    box-sizing: inherit;
}

.navlistItem {
    visibility: visible;
    padding-left: 16px;
    padding-right: 16px;
    margin-left: 0;
    font-size: 16px;
    vertical-align: top;
    list-style: none;
    color: rgb(87, 87, 87);
    display: inline-block;
    /* position: relative; */
    /* font-family: 'Open Sans', arial, sans-serif; */
    text-transform: none;
    letter-spacing: normal;
    box-sizing: inherit;
    text-align: -webkit-match-parent;
    line-height: 24px;
    -webkit-font-smoothing: antialiased;
    -webkit-text-size-adjust: 100%;
    -webkit-tap-highlight-color: rgba(0,0,0,0);
    box-sizing: inherit;
    cursor: pointer;
    background-color: transparent;
    border-color: transparent;
}

.gif {
    flex-direction: row;
    justify-content: center;
    display: flex;
    padding-top: 0px;
    margin-bottom: 0px;
